.edit__save {
    margin-right: 10px;
}

.popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Затемнений фон */
    display: flex;
    justify-content: center;
    align-items: center;
}
  
.popup-content {
    background-color: white;
    padding: 20px;
    border-radius: 5px;
}